import React from 'react';
import  { ListGroup, ListGroupItem, Table  } from 'react-bootstrap'

class Sale extends React.Component {
  constructor(props) {
    super(props);  
    this.state = { sale:{}, loading: true}; 
}

componentDidMount(){
  fetch(`https://lit-retreat-47963.herokuapp.com/api/sales/${this.props.id}`) 
    .then(response => {
      if (response.status >= 200 && response.status <= 299) {
        return response.json();
      } else {
        throw Error(response.statusText);
      }
    })
    .then(data => {
        if(data._id){
          this.props.viewedSale(data._id);
          this.setState({sale: data, loading:false});
        }
    })
    .catch(error => {
      // Handle the error
      console.log(error);
      this.setState({sale: "", loading:false});
    });
}
componentDidUpdate(prevProps){
  if (prevProps.id !== this.props.id){
    this.setState({loading: true});
    fetch(`https://lit-retreat-47963.herokuapp.com/api/sales/${this.props.id}`)
    .then(response => {
      if (response.status >= 200 && response.status <= 299) {
        return response.json();
      } else {
        throw Error(response.statusText);
      }
    })
    .then(data => {
        if(data._id){
          this.props.viewedSale(data._id);
          this.setState({sale: data, loading:false});
        }
    })
    .catch(error => {
      // Handle the error
      console.log(error);
      this.setState({sale: "", loading:false});
    });
  }
}

itemTotal(items){
  let total = 0;
  items.forEach(element => {
    total += (element.price * element.quantity);
  });
  return total.toFixed(2);
}

render() {
  if (this.state.loading) {
    return <h4>Loading</h4>; // NOTE: This can be changed to render a <Loading /> Component for a better user experience
   } else {
    if (this.state.sale._id) {
    return (<div>
    <h1>Sale: {this.state.sale._id}{/* <!-- TODO: Show the Sale ID --> */}</h1>
    <h2>Customer</h2>
    <ListGroup>
    <ListGroupItem><strong>email:</strong> {this.state.sale.customer.email}{/* <!-- TODO: Show the Customer Email --> */}</ListGroupItem>
    <ListGroupItem><strong>age:</strong> {this.state.sale.customer.age} {/* <!-- TODO: Show the Customer Age --> */}</ListGroupItem>
    <ListGroupItem><strong>satisfaction:</strong> {this.state.sale.customer.satisfaction}{/* <!-- TODO: Show the Customer Satisfaction --> */} /
   5</ListGroupItem>
    </ListGroup>
    <h2> Items: ${this.itemTotal(this.state.sale.items)}{/* <!-- TODO: Show the Item Total --> */}</h2>
    <Table>
 <thead>
 <tr>
 <th>Product Name</th>
 <th>Quantity</th>
 <th>Price</th>
 </tr>
 </thead>
 <tbody>
 {/* <!-- TODO: loop through all of the sale items and display their data --> */}
 {this.state.sale.items.map(function(item, index){
          return (
            <tr key={index}>
                <td>{item.name}</td>
                <td>{item.quantity}</td>
                <td>${item.price}</td>
            </tr>
        );
        })}
 </tbody>
 </Table>
 </div>);
 } else {
 return <div><h1>Unable to find Sale</h1><p>id: {this.props.id}</p></div>
 }
}
  }//end of render
}//end of Sale

export default Sale;